/**
* 执法过程-案件视频组件
* @module 系统
* @desc 执法过程绑定视频
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" append-to-body center
               top='5vh'
               width="80%">

        <div class="wrop-height">
            <el-tabs v-model="activeName">
                <!-- 现场检查 -->
                <el-row>
                    <el-col :span="16">
                        <el-row>
                            <el-form :label-position="labelPosition" :model="dataObj" :rules="rules" label-width="110px"
                                     ref="ruleForm">
                                <el-col :span="24">
                                    <el-form-item label="执法过程段：" prop="caseStage">
                                        <el-select @change="selectstap" class="select-width"
                                                   placeholder="选择执法全过程记录" v-model="dataObj.caseStage">
                                            <el-option :key="item.dictValue" :label="item.dictName"
                                                       :value="item.dictValue" v-for="item in stapArr"></el-option>
                                        </el-select>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="24">
                                    <el-form-item label="选择时间段：" prop="timestap">
                                        <el-date-picker end-placeholder="结束日期" format="yyyy-MM-dd hh:mm"
                                                        range-separator="至" start-placeholder="开始日期"
                                                        type="datetimerange"
                                                        v-model="dataObj.timestap"></el-date-picker>
                                        <!-- <el-date-picker v-model="dataObj.timestap"  value-format='yyyy-MM-dd' format="yyyy-MM-dd hh:mm"   type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" style="width:100%"></el-date-picker>  -->
                                    </el-form-item>
                                </el-col>
                                <el-col :span="24">
                                    <el-form-item label="其他信息：">
                                        <el-input :rows="5" placeholder="请输入其他信息内容" type="textarea"
                                                  v-model="dataObj.videoDesc"></el-input>
                                    </el-form-item>
                                </el-col>
                            </el-form>
                        </el-row>
                    </el-col>
                    <el-col :span="8">
                        <div class="video">
                            <video :src="videoSrc" controls height="250" width="250">
                                <source type="video/mp4">
                            </video>
                        </div>
                    </el-col>
                    <el-col :span="24">
                        <search-form :searchObj="searchObj" @searchList="searchList"/>
                    </el-col>
                    <el-col :span="24">
                        <el-table :data="list" :header-cell-style="{background:'#e9eff8'}"
                                  :row-style="{cursor:'pointer'}"
                                  @row-click="seeFunction" @selection-change="handleSelectionChange" border ref="table"
                                  size="mini">
                            <el-table-column type="selection" width="55"></el-table-column>
                            <el-table-column align="center" label="视频名称" prop="videoName" show-overflow-tooltip/>
                            <el-table-column align="center" label="视频URL" prop="videoUrl" show-overflow-tooltip/>
                            <el-table-column align="center" label="视频大小" prop="videoSize" show-overflow-tooltip/>
                            <el-table-column align="center" label="视频时长" prop="videoTime" show-overflow-tooltip/>
                            <el-table-column align="center" label="执法人员" prop="officials" show-overflow-tooltip/>
                            <el-table-column align="center" label="操作" width="100">
                                <template slot-scope="scope">
                                    <el-tooltip class="item" content="播放视频" effect="dark" placement="top-end">
                                        <el-link @click.stop="seeFunction(scope.row)" class="el-icon-video-play"
                                                 size="mini" type="success"></el-link>
                                    </el-tooltip>
                                </template>
                            </el-table-column>
                        </el-table>
                        <!-- <i-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total"
                                    @getData="getAccountList"
                                    style="margin-top:10px"/> -->
                    </el-col>
                </el-row>

            </el-tabs>
        </div>
        <span class="dialog-footer" slot="footer">
                <el-button @click="closeDialog" size="small" type="info">
                    <i class="iconfont icon-back"></i>取 消
                </el-button>
                <el-button @click="submitForm('ruleForm')" size="small" type="primary">
                    <i class="iconfont icon-save"></i>确定关联
                </el-button>
            </span>
    </el-dialog>

</template>

<script>
    import {formatDate, YESORNO} from "../../../until/wordBook";
    import {
        apiCaseCaseVideoAdd,
        apiCaseCaseVideoQueryByCaseStage,
        apiCaseVideoQueryUnrelatedCaseVideo,
        apiCaseVideoSearch
    } from "../../../api/case";
    import searchForm from '../../../components/LjSearchForm'
    import iPagination from '../../../components/LjPagination'

    export default {
        name: "caseVideo",
        props: {
            editId: {
                type: String,
                default: ''
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: ""
            },

        },
        data() {
            return {
                tableData: [],
                labelPosition: 'right',
                dataObj: {
                    caseId: '',
                    caseStage: 'sceneCheck',
                    startTime: '',
                    endTime: '',
                    videoDesc: '',
                    vids: '',
                    timestap: null,
                },
                videoSrc: '',

                list: [],
                list2: [],
                total: 0,
                searchObj: null,
                form: {
                    pageSize: 20,
                    pageNum: 1,
                },

                activeName: 'sceneCheck',
                stapArr: [],
                rules: {

                    caseStage: [
                        {required: true, message: '请选择案件过程', trigger: 'blur'}
                    ],
                    timestap: [
                        {required: true, message: '请选择日期', trigger: 'blur'}
                    ]


                }
            }
        },
        methods: {
            closeDialog() {
                this.$emit('update:show', false)
            },
            selectstap(val) {
                let data = {caseStage: val, caseId: this.editId}
                apiCaseCaseVideoQueryByCaseStage(data).then(res => {
                    if (res.data) {
                        let alreadyarr = res.data.caseVideoList;
                        this.list = this.list2.concat(alreadyarr);
                        this.list.reverse()
                        this.total = this.list.length;
                        this.$nextTick(function () {
                            for (let i in alreadyarr) {
                                this.$refs.table.toggleRowSelection(this.list[i], true)
                            }

                        })
                    }
                })
            },

            //查询+搜索
            getAccountList() {
                apiCaseVideoSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                })
            },
            //保存
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.dataObj.startTime = Date.parse(this.dataObj.timestap[0])
                        this.dataObj.endTime = Date.parse(this.dataObj.timestap[1]);
                        this.dataObj.caseId = this.editId;
                        apiCaseCaseVideoAdd(this.dataObj).then(res => {
                            this.closeDialog();
                            this.$message({
                                type: 'success',
                                message: '关联成功!'
                            });
                        })

                    } else {
                        this.$message({
                            type: 'error',
                            message: '请填写完整!'
                        });
                    }
                });
            },

            //搜索
            searchList(data) {  //data就是接收的this.form数据
                data.pageSize = this.form.pageSize;
                if (this.dataObj.timestap) {
                    data.startShootTime = Date.parse(this.dataObj.timestap[0]);
                    data.endShootTime = Date.parse(this.dataObj.timestap[1]);
                } else {
                    data.startShootTime = '';
                    data.endShootTime = '';
                }

                this.form.pageNum = 1;
                apiCaseVideoQueryUnrelatedCaseVideo(data).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                })


            },
            //查看
            seeFunction(row) {
                this.videoSrc = row.videoUrl
            },
            //批量
            handleSelectionChange(valarr) {
                let arr = []
                valarr.forEach(item => {
                    arr.push(item.vid)
                })
                let arrstr = arr.join(',')
                this.dataObj.vids = arrstr;//字符串id
            },

            //弹框弹出时获取data给form
            initPage() {

                this.dataObj.caseId = '';
                this.dataObj.startTime = '';
                this.dataObj.endTime = '';
                this.dataObj.videoDesc = '';
                this.dataObj.vids = '';
                this.dataObj.timestap = null,
                    //未绑定视频
                    apiCaseVideoQueryUnrelatedCaseVideo({pageSize: 20, pageNum: 1, caseId: this.editId}).then(res => {
                        this.list2 = res.data;
                        this.total = res.total;
                    })
                //已绑定视频
                apiCaseCaseVideoQueryByCaseStage({caseStage: 'sceneCheck', caseId: this.editId}).then(res => {
                    if (res.data) {
                        let alreadyarr = res.data.caseVideoList;
                        this.list = this.list2.concat(alreadyarr);
                        this.list.reverse()
                        this.total = this.list.length;
                        this.$nextTick(function () {
                            for (let i in alreadyarr) {
                                this.$refs.table.toggleRowSelection(this.list[i], true)
                            }

                        })
                    }
                })
                //阶段字典
                this.$until.workBookApi('videoProcess').then(res => {  //视频阶段
                    this.stapArr = res
                });


            }
        },
        components: {
            searchForm, iPagination
        },
        filters: {
            YESORNO(val) {
                return YESORNO[val]
            },
            formatDate(val) {
                return formatDate(val, 'yyyy-MM-dd')
            }
        },

        //监听，如果show为true,则执行initPage()
        watch: {
            'show'(value) {
                if (value) {
                    this.initPage();

                }
            }
        },


    }
</script>

<style lang="less" scoped>
    .concss {
        font-weight: bold
    }

    .fontcss {
        font-family: "Microsoft YaHei", "微软雅黑", "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", Arial, sans-serif;
        color: #333;
        font-weight: 500;
        display: inline-block;
        min-width: 300px;
        border-radius: 5px;
        padding: 0 10px;
        border-bottom: 1px dashed #C5C5C5
    }

    .detail-block {
        font-size:  @fontSize +4;

        h3 {
            text-align: center;
        }

        .enforceCode {
            float: right;
        }

        .enforceRight-block {
            padding: 10px 20px 0 20px;

            .enforceRight {
                text-indent: 2em;
                margin: 10px 0;
            }
        }

        .isDisable {
            padding: 0 20px;
            font-size:  @fontSize ;
            color: #888;
        }
    }

    .video {
        width: 250px;
        height: 250px;
        border: 1px solid gray;
        margin-left: 15px
    }

    .wrop-height {
        min-height: 500px
    }

    .select-width {
        width: 100%
    }
</style>
