/**
* 执法过程-案件视频组件
* @module 系统
* @desc 一般案件绑定视频
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <!-- 表格 -->
        <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" :row-style="{cursor:'pointer'}"
                  @row-click="seeFunction" @selection-change="handleSelectionChange" border size="mini"
                  v-loading="loading">
            <!-- <el-table-column type="selection" width="55"></el-table-column> -->
            <el-table-column align="center" label="案发时间" prop="crimeTime" width="120">
                <template slot-scope="scope" v-if='scope.row.crimeTime'>
                    {{scope.row.crimeTime | formatDate }}
                </template>
            </el-table-column>
            <el-table-column align="center" label="案发地点" prop="crimeLocal" show-overflow-tooltip/>
            <el-table-column align="center" label="案件来源" prop="caseSource" show-overflow-tooltip/>
            <el-table-column align="center" label="办案单位" prop="handlingUnit" show-overflow-tooltip/>
            <el-table-column align="center" label="主办案人名称" prop="investigatorsName" show-overflow-tooltip/>
            <el-table-column align="center" label="当事人" prop="party" show-overflow-tooltip width="120"/>
            <el-table-column align="center" label="当事人类型" prop="partyType" show-overflow-tooltip width="120"/>
            <el-table-column align="center" label="案件证据" prop="caseEvidence" show-overflow-tooltip/>
            <el-table-column align="center" label="案件状态" prop="caseStatus" show-overflow-tooltip width="120"/>

            <el-table-column align="center" label="操作" width="100">
                <template slot-scope="scope">
                    <el-tooltip class="item" content="查看" effect="dark" placement="top-end">
                        <el-link @click.stop="seeFunction(scope.row)" class="el-icon-view btn-link"
                                 type="success"><i></i></el-link>
                    </el-tooltip>
                    <el-tooltip class="item" content="关联视频" effect="dark" placement="top-end">
                        <el-link @click.stop="connect(scope.row)" circle class="el-icon-link" size="mini"
                                 type="success"></el-link>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>

        <lj-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total"
                       @getData="getAccountList" class="page-css"/>

        <!-- 关联操作 -->
        <seeContent :editId='seeObj.editId' :show.sync='seeObj.show' :title='seeObj.title'></seeContent>
        <!-- 查看关联 -->
        <seeContent2 :editId='seeObj2.editId' :show.sync='seeObj2.show' :title='seeObj2.title'></seeContent2>
    </div>
</template>

<script>
    import {apiCaseCaseVideoDelete, apiormalCaseSNearch} from "../../../api/case";
    import LjPagination from '../../../components/LjPagination'
    import LjSearchForm from '../../../components/LjSearchForm'
    import {formatDate, IFSEX} from "../../../until/wordBook";
    import seeContent from "./connect";
    import seeContent2 from "./seeConnect";

    export default {
        name: "caselist",
        data() {
            return {
                loading: true,
                //关联
                seeObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                //查看数据
                seeObj2: {
                    show: false,
                    title: '',
                    editId: null
                },
                //搜索组件数据
                searchObj: null,
                form: {
                    pageSize: 20,
                    pageNum: 1,
                    caseType: 1 //1代表一般案件   2 普通案件
                },
                total: 0,
                input: '',
                list: [],
                rowdata: {},
                idarr: [],
                arrstr: ''
            }
        },
        components: {
            LjPagination, LjSearchForm, seeContent, seeContent2
        },

        methods: {
            handleSelectionChange(valarr) {
                let arr = []
                valarr.forEach(item => {
                    arr.push(item.cvid)
                })
                let arrstr = arr.join(',')
                this.arrstr = arrstr;
            },
            //查询+搜索
            getAccountList() {
                this.loading = true;
                apiormalCaseSNearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                    this.loading = false;
                })
            },
            //关联操作
            connect(row) {
                this.seeObj.editId = row.nmId;
                this.seeObj.show = true;
                this.seeObj.title = '关联视频操作';
            },
            //查看
            seeFunction(row) {
                this.seeObj2.editId = row.nmId;
                this.seeObj2.show = true;
                this.seeObj2.title = '查看关联视频';
            },

            //删除
            delateFunction(row) {
                this.$confirm('此操作将删除条数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    apiCaseCaseVideoDelete(row.cvid).then(res => {
                        this.getAccountList();
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    });
                })
            },

            //搜索
            searchList(data) {  //data就是接收的this.form数据
                data.pageSize = this.form.pageSize;
                this.form.pageNum = 1;
                apiormalCaseSNearch(data).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                })

            },

        },
        filters: {
            ifsex(val) {
                return IFSEX[val]
            },
            formatDate(value) {
                return formatDate(value, 'yyyy-MM-dd')
            }

        },
        computed: {},
        mounted() {

        },

        created() {
            this.getAccountList();
        },
    }
</script>

<style scoped>
    .btn-link {
        margin: 0 10px
    }

    .page-css {
        margin-top: 10px
    }
</style>
