/**
* 执法过程-案件视频组件
* @module 系统
* @desc 查看
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" append-to-body center
               top='5vh'
               width="80%">

        <div class="wrop-div">

            <el-row>
                <el-col :span="16">
                    <el-row>

                        <el-form :label-position="labelPosition" label-width="100px">
                            <el-col :span="24">
                                <el-form-item label="执法过程：">
                                    <el-tabs @tab-click="handleClick" type="card" v-model="activeName">
                                        <el-tab-pane :key='item.dictValue' :label="item.dictName"
                                                     :name="item.dictValue" v-for="item in stapArr"></el-tab-pane>
                                    </el-tabs>
                                </el-form-item>
                            </el-col>
                            <div class="ifnull" v-if="!formlist">暂无数据</div>
                            <div v-else>
                                <el-col :span="12">
                                    <el-form-item label="开始时间：">
                                        <span class="fontcss">{{formlist.startTime }}</span>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="结束时间：">
                                        <span class="fontcss">{{formlist.endTime}}</span>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="24">
                                    <el-form-item label="其他信息：">
                                        <span class="fontcss">{{formlist.videoDesc}}</span>
                                    </el-form-item>
                                </el-col>
                            </div>


                        </el-form>
                    </el-row>

                </el-col>
                <el-col :span="8">
                    <div class="video">
                        <video :src="videoSrc" controls height="250" width="250">
                            <!-- <source src="movie.ogg"  type="video/ogg"> -->
                        </video>
                    </div>
                </el-col>

                <el-col :span="24">
                    <el-table :data="form.caseVideoList" :header-cell-style="{background:'#e9eff8'}"
                              :row-style="{cursor:'pointer'}" @row-click="seeFunction" border size="mini">
                        <!-- <el-table-column type="selection" width="55"></el-table-column> -->
                        <el-table-column align="center" label="视频名称" prop="videoName" show-overflow-tooltip/>
                        <el-table-column align="center" label="视频URL" prop="videoUrl" show-overflow-tooltip
                                         width="150"/>
                        <el-table-column align="center" label="视频大小" prop="videoSize" show-overflow-tooltip/>
                        <el-table-column align="center" label="视频时长" prop="videoTime" show-overflow-tooltip/>
                        <el-table-column align="center" label="执法人员" prop="officials" show-overflow-tooltip/>
                        <el-table-column align="center" label="操作" width="100">
                            <template slot-scope="scope">
                                <el-tooltip class="item" content="播放视频" effect="dark" placement="top-end">
                                    <el-link @click.stop="seeFunction(scope.row)" class="el-icon-video-play"
                                             size="mini" type="success"></el-link>
                                </el-tooltip>
                            </template>
                        </el-table-column>
                    </el-table>
                    <!-- <i-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total"
                                @getData="getAccountList"
                                style="margin-top:10px"/> -->
                </el-col>
            </el-row>

        </div>
        <span class="dialog-footer" slot="footer">
                <el-button @click="closeDialog" size="small" type="info">
                    <i class="iconfont icon-back"></i>取 消
                </el-button>
                <el-button @click="submitForm" size="small" type="primary">
                    <i class="iconfont icon-save"></i>确定关联
                </el-button>
            </span>
    </el-dialog>

</template>

<script>
    import {formatDate, YESORNO} from "../../../until/wordBook";
    import {apiCaseCaseVideoAdd, apiCaseCaseVideoQueryByCaseStage, apiCaseVideoSearch} from "../../../api/case";
    import searchForm from '../../../components/LjSearchForm'
    import iPagination from '../../../components/LjPagination'

    export default {
        name: "seeConnect",
        props: {
            editId: {
                type: String,
                default: ''
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: ""
            },

        },
        data() {
            return {
                labelPosition: 'right',
                list: [],
                videoUrl: '',
                total: 0,
                searchObj: null,
                form: {
                    pageSize: 20,
                    pageNum: 1,
                },
                formlist: {},
                form: [],
                dataObj: {
                    caseId: '',
                    caseStage: '',
                    startTime: '',
                    endTime: '',
                    videoDesc: '',
                    vids: ''
                },
                videoSrc: '',
                activeName: 'sceneCheck',
                stapArr: []
            }
        },
        methods: {
            closeDialog() {
                this.$emit('update:show', false)
            },
            seeFunction(row) {
                this.videoSrc = row.videoUrl
            },
            //tab选择
            handleClick(tab, event) {
                let data = {caseStage: tab.name, caseId: this.editId}
                apiCaseCaseVideoQueryByCaseStage(data).then(res => {
                    if (res.data) {
                        this.formlist = res.data;
                        if (this.formlist.startTime == null) {
                            this.formlist.startTime = '无'
                        } else {
                            formatDate(this.form.startTime, 'yyyy-MM-dd')
                        }
                        if (this.formlist.endTime == null) {
                            this.formlist.endTime = '无'
                        } else {
                            formatDate(this.formlist.endTime, 'yyyy-MM-dd')
                        }
                    } else {
                        this.formlist = null
                    }
                })
            },
            //保存
            submitForm() {
                this.dataObj.startTime = Date.parse(this.timestap[0])
                this.dataObj.endTime = Date.parse(this.timestap[1]);
                this.dataObj.caseId = this.editId;
                apiCaseCaseVideoAdd(this.dataObj).then(res => {
                    this.closeDialog();
                    // this.$parent.getList();
                    this.$message({
                        type: 'success',
                        message: '关联成功!'
                    });
                })

            },
            //搜索
            searchList(data) {  //data就是接收的this.form数据
                data.pageSize = this.form.pageSize;
                this.form.pageNum = 1;
                apiCaseVideoSearch(data).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                })
            },
            //弹框弹出时获取data给form
            initPage() {
                apiCaseCaseVideoQueryByCaseStage({caseStage: 'sceneCheck', caseId: this.editId}).then(res => {
                    if (res.data) {
                        this.formlist = res.data;
                        if (this.formlist.startTime == null) {
                            this.formlist.startTime = '无'
                        } else {
                            formatDate(this.formlist.startTime, 'yyyy-MM-dd')
                        }
                        if (this.formlist.endTime == null) {
                            this.formlist.endTime = '无'
                        } else {
                            formatDate(this.formlist.endTime, 'yyyy-MM-dd')
                        }
                    } else {
                        this.formlist = null
                    }
                })
                //案件的阶段字典
                this.$until.workBookApi('videoProcess').then(res => {
                    this.stapArr = res
                });

            }
        },
        components: {
            searchForm, iPagination
        },
        filters: {
            YESORNO(val) {
                return YESORNO[val]
            },
            formatDate(val) {
                return formatDate(val, 'yyyy-MM-dd')
            }
        },

        //监听，如果show为true,则执行initPage()
        watch: {
            'show'(value) {
                if (value) {
                    this.initPage();

                }
            }
        },


    }
</script>

<style lang="less" scoped>
    .concss {
        font-weight: bold
    }

    .ifnull {
        width: 100%;
        height: 100px;
        text-align: center;
        line-height: 100px
    }

    .fontcss {
        font-family: "Microsoft YaHei", "微软雅黑", "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", Arial, sans-serif;
        color: #333;
        font-weight: 800;
        display: inline-block;
        min-width: 300px;
        border-radius: 5px;
        padding: 0 10px;
        // border-bottom: 1px dashed #C5C5C5
    }

    .detail-block {
        font-size:  @fontSize + 4;

        h3 {
            text-align: center;
        }

        .enforceCode {
            float: right;
        }

        .enforceRight-block {
            padding: 10px 20px 0 20px;

            .enforceRight {
                text-indent: 2em;
                margin: 10px 0;
            }
        }

        .isDisable {
            padding: 0 20px;
            font-size:  @fontSize ;
            color: #888;
        }
    }

    .video {
        width: 250px;
        height: 250px;
        border: 1px solid gray;
        margin: 15px
    }

    .wrop-div {
        min-height: 500px
    }
</style>
